import AsyncStorage from "@react-native-async-storage/async-storage";
import { API_CONFIG, STORAGE_CONFIG, ERROR_CODES } from "../constants/config";
import { ApiResponse } from "../types";

// ËØ∑Ê±ÇÈÖçÁΩÆÊé•Âè£
interface RequestConfig {
  headers?: Record<string, string>;
  timeout?: number;
  requireAuth?: boolean;
}

// HTTPÊñπÊ≥ïÁ±ªÂûã
type HttpMethod = "GET" | "POST" | "PUT" | "DELETE" | "PATCH";

class ApiClient {
  private baseURL: string;
  private defaultTimeout: number;

  constructor() {
    this.baseURL = API_CONFIG.BASE_URL;
    this.defaultTimeout = API_CONFIG.TIMEOUT;
  }

  /**
   * Ëé∑ÂèñÂ≠òÂÇ®ÁöÑËÆ§ËØÅ‰ª§Áâå
   */
  private async getAuthToken(): Promise<string | null> {
    try {
      return await AsyncStorage.getItem(STORAGE_CONFIG.KEYS.AUTH_TOKEN);
    } catch (error) {
      console.error("Ëé∑ÂèñËÆ§ËØÅ‰ª§ÁâåÂ§±Ë¥•:", error);
      return null;
    }
  }

  /**
   * ËÆæÁΩÆËÆ§ËØÅ‰ª§Áâå
   */
  async setAuthToken(token: string): Promise<void> {
    try {
      await AsyncStorage.setItem(STORAGE_CONFIG.KEYS.AUTH_TOKEN, token);
    } catch (error) {
      console.error("ËÆæÁΩÆËÆ§ËØÅ‰ª§ÁâåÂ§±Ë¥•:", error);
    }
  }

  /**
   * ÁßªÈô§ËÆ§ËØÅ‰ª§Áâå
   */
  async removeAuthToken(): Promise<void> {
    try {
      await AsyncStorage.removeItem(STORAGE_CONFIG.KEYS.AUTH_TOKEN);
    } catch (error) {
      console.error("ÁßªÈô§ËÆ§ËØÅ‰ª§ÁâåÂ§±Ë¥•:", error);
    }
  }

  /**
   * ÊûÑÂª∫ËØ∑Ê±ÇÂ§¥
   */
  private async buildHeaders(
    config?: RequestConfig
  ): Promise<Record<string, string>> {
    const headers: Record<string, string> = {
      "Content-Type": "application/json",
      Accept: "application/json",
      ...config?.headers,
    };

    // Â¶ÇÊûúÈúÄË¶ÅËÆ§ËØÅÔºåÊ∑ªÂä†AuthorizationÂ§¥
    if (config?.requireAuth !== false) {
      const token = await this.getAuthToken();
      if (token) {
        headers.Authorization = `Bearer ${token}`;
      }
    }

    return headers;
  }

  /**
   * ÂèëÈÄÅHTTPËØ∑Ê±Ç
   */
  private async request<T = any>(
    method: HttpMethod,
    endpoint: string,
    data?: any,
    config?: RequestConfig
  ): Promise<ApiResponse<T>> {
    try {
      const url = `${this.baseURL}${endpoint}`;
      const headers = await this.buildHeaders(config);
      const timeout = config?.timeout || this.defaultTimeout;

      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), timeout);

      const requestOptions: RequestInit = {
        method,
        headers,
        signal: controller.signal,
      };

      // ‰∏∫POST„ÄÅPUT„ÄÅPATCHËØ∑Ê±ÇÊ∑ªÂä†body
      if (data && ["POST", "PUT", "PATCH"].includes(method)) {
        requestOptions.body = JSON.stringify(data);
      }

      console.log(`üöÄ APIËØ∑Ê±Ç: ${method} ${url}`);

      const response = await fetch(url, requestOptions);
      clearTimeout(timeoutId);
      const responseData = await response.json();

      console.log(`üì° APIÂìçÂ∫î: ${response.status}`, responseData);

      if (!response.ok) {
        return {
          success: false,
          data: undefined,
          error: {
            code: response.status.toString(),
            message: responseData.message || `HTTP ${response.status}`,
            details: responseData.details,
          },
        };
      }

      return {
        success: true,
        data: responseData.data || responseData,
        error: undefined,
      };
    } catch (error: any) {
      console.error(`‚ùå APIÈîôËØØ: ${method} ${endpoint}`, error);

      let errorMessage = "ÁΩëÁªúËøûÊé•Â§±Ë¥•";
      let errorCode = ERROR_CODES.NETWORK_ERROR;

      if (error.name === "AbortError") {
        errorMessage = "ËØ∑Ê±ÇË∂ÖÊó∂";
        errorCode = ERROR_CODES.TIMEOUT;
      } else if (error.message) {
        errorMessage = error.message;
      }

      return {
        success: false,
        data: undefined,
        error: {
          code: errorCode,
          message: errorMessage,
          details: error.stack,
        },
      };
    }
  }

  /**
   * GETËØ∑Ê±Ç
   */
  async get<T = any>(
    endpoint: string,
    config?: RequestConfig
  ): Promise<ApiResponse<T>> {
    return this.request<T>("GET", endpoint, undefined, config);
  }

  /**
   * POSTËØ∑Ê±Ç
   */
  async post<T = any>(
    endpoint: string,
    data?: any,
    config?: RequestConfig
  ): Promise<ApiResponse<T>> {
    return this.request<T>("POST", endpoint, data, config);
  }

  /**
   * PUTËØ∑Ê±Ç
   */
  async put<T = any>(
    endpoint: string,
    data?: any,
    config?: RequestConfig
  ): Promise<ApiResponse<T>> {
    return this.request<T>("PUT", endpoint, data, config);
  }

  /**
   * DELETEËØ∑Ê±Ç
   */
  async delete<T = any>(
    endpoint: string,
    config?: RequestConfig
  ): Promise<ApiResponse<T>> {
    return this.request<T>("DELETE", endpoint, undefined, config);
  }

  /**
   * PATCHËØ∑Ê±Ç
   */
  async patch<T = any>(
    endpoint: string,
    data?: any,
    config?: RequestConfig
  ): Promise<ApiResponse<T>> {
    return this.request<T>("PATCH", endpoint, data, config);
  }

  /**
   * ‰∏ä‰º†Êñá‰ª∂
   */
  async uploadFile<T = any>(
    endpoint: string,
    file: any,
    config?: RequestConfig
  ): Promise<ApiResponse<T>> {
    try {
      const url = `${this.baseURL}${endpoint}`;
      const headers = await this.buildHeaders(config);

      // ÁßªÈô§Content-TypeÔºåËÆ©ÊµèËßàÂô®Ëá™Âä®ËÆæÁΩÆ
      delete headers["Content-Type"];

      const formData = new FormData();
      formData.append("file", file);

      const controller = new AbortController();
      const timeoutId = setTimeout(
        () => controller.abort(),
        config?.timeout || this.defaultTimeout
      );

      const response = await fetch(url, {
        method: "POST",
        headers,
        body: formData,
        signal: controller.signal,
      });

      clearTimeout(timeoutId);
      const responseData = await response.json();

      if (!response.ok) {
        return {
          success: false,
          data: undefined,
          error: {
            code: response.status.toString(),
            message: responseData.message || `HTTP ${response.status}`,
            details: responseData.details,
          },
        };
      }

      return {
        success: true,
        data: responseData.data || responseData,
        error: undefined,
      };
    } catch (error: any) {
      return {
        success: false,
        data: undefined,
        error: {
          code: ERROR_CODES.NETWORK_ERROR,
          message: error.message || "Êñá‰ª∂‰∏ä‰º†Â§±Ë¥•",
          details: error.stack,
        },
      };
    }
  }
}

// ÂàõÂª∫Âçï‰æãÂÆû‰æã
export const apiClient = new ApiClient();

// ÂØºÂá∫Â∑•ÂÖ∑ÂáΩÊï∞
export const setApiAuthToken = (token: string) => apiClient.setAuthToken(token);
export const removeApiAuthToken = () => apiClient.removeAuthToken();
